.modal {
    z-index: 100;
    position:fixed;
    top: 9vh;
    left: 10%;
    width: 80%;
    border-radius: 15px;
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 8px;
    display:flow-root;
    overflow-x:auto;

  }
  
  .modal__header {
    width: 200%;
    padding: 1rem 0.5rem;
    background: #7643DF;
    color: rgb(255, 255, 255);
  }
  
  .modal__header h2 {
    margin: 0.5rem;
    color: rgb(255, 255, 255);

  }
  
  .modal__content {
    padding: 1rem 0.5rem;
    display:flow-root;
    
  }
  
  .modal__footer {
    padding: 1rem 0.5rem;
  }
  
  @media (min-width: 768px) {
    .modal {
      left: calc(50% - 20rem);
      width: 40rem;
    }
  }
  
  .modal-enter {
    transform: translateY(-10rem);
    opacity: 0;
  }
  
  .modal-enter-active {
    transform: translateY(0);
    opacity: 1;
    transition: all 200ms;
  }
  
  .modal-exit {
    transform: translateY(0);
    opacity: 1;
  }
  
  .map-container{
    height:14rem;
    width: 100%;
  }
  .modal-exit-active {
    transform: translateY(-10rem);
    opacity: 0;
    transition: all 200ms;
  }
  