.skills__container {
    grid-template-columns: repeat(4, 300px);
    column-gap: 2rem;
    justify-content: center;
    padding-left: 5px;
}

.skills__content {
    background-image: url(https://img.freepik.com/free-vector/pastel-watercolor-banner_91008-232.jpg?w=1060&t=st=1688970330~exp=1688970930~hmac=fe7095a9378b36f0f2869436a1f47bb43e55e7c3b98d14ae6779ad413c7de165);
    /* background-size: 200% auto; */
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 2rem 4rem;
    border-radius: 1.25rem;   
    background-size:cover;
    background-repeat: no-repeat;
}
.skills__content:hover{
    transform: translate3D(0,-1px,0) scale(1.03);
}


.skills__title {
    font-size: 20px;
    font-weight: 18px;
    text-align: center;
    margin-bottom: var(--mb-1-5);
}


.skills__box {
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}

.skills__group {
    display: grid;
    align-items: flex-start;
    row-gap: 1rem;
}

.skills__data {
    display: flex;
    column-gap: 0.5rem;
}

.skills svg {
    font-size: 1rem;
    color: var(--title-color);
}

.skills__name {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    line-height: 18px;
}

.skills__level {
    font-size: var(--smaller-font-size);
}

/* BREAK POINTS */
@media screen and (max-width: 992px) {
   .skills__container {
    grid-template-columns: max-content;
    row-gap: 2rem;
   }
}

@media screen and (max-width: 576px) {
    .skills__container {
        grid-template-columns: 1fr;
    }

    .skills__content {
        padding: 1.5rem;
    }
}

@media screen and (max-width: 350px) {
    .skills__box {
        column-gap: 1.25rem;
    }

    .skills__name {
        font-size: var(--small-font-size);
    }
}